@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font: bold 18px/24px "Open Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}
h3 {
  font: bold 25px/30px "Open Sans", sans-serif;
}
.app {
  text-align: center;
}

.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.header{display:flex;background-color:rgba(0,0,0,0.3);justify-content:center;position:absolute;left:0;right:0;z-index:9999}.header ul{max-width:400px;width:100%;display:flex;justify-content:space-between}.header ul li{display:inline-block;line-height:55px}.header ul li a{color:white}


.about {
    background: #2B2B2B;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about__wapper {
    padding: 0 10%;
    display: flex;
    flex-direction: row;
}
.about__picture {
    width: 25%;
    flex-shrink: 0;
    text-align: left;
}
.about__picture > img {
    width: 120px;
    height: 120px;
}
.about__info {
    text-align: left;
}
.info__tab>h2 {
    font: bold 22px/30px 'Open Sans', sans-serif;
    color: #fff;
    margin-bottom: 12px;
}

.info__tab>p {
    line-height: 30px;
    color: #7A7A7A;
}

.info__tab>p>span {
    display: block;
}
.banner {
    background-image: url(/static/media/bg.ba105921.jpg);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: #3f5e8c;
}

.banner__wapper {
    width: 85%;
    text-align: center;
    padding-bottom: 30px;
}

.banner__title {
    font: bold 90px/1.1em 'Open Sans', sans-serif;
    color: #fff;
    letter-spacing: -2px;
    margin: 0 auto 18px auto;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, .8);
}

.banner__desc {
    font-size: 16px;
    color: #A8A8A8;
    margin: 0 auto;
    width: 70%;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, .5);
}

.banner__desc span {
    color: white;
}

.banner__social {
    margin: 24px 0;
    padding: 0;
    font-size: 30px;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, .8);
}
.banner__social a{
    display: inline-block;
    margin: 0 15px;
    padding: 0;
    color: white;
}
.contact{background:#191919;padding-top:96px;padding-bottom:102px;color:#636363;text-align:left}.contact .contact__head{margin-bottom:25px}.contact input,.contact textarea,.contact select{padding:10px 20px;color:#eee;background:#373233;margin-bottom:20px;border:0;outline:none;font-size:15px;line-height:24px;width:65%;resize:none}.contact label{font:bold 15px/24px "Open Sans", sans-serif;float:left;margin:10px 0;color:#ebeeee;display:inline-block;width:26%}.contact button{text-transform:uppercase;letter-spacing:3px;color:#fff;background:#0D0D0D;border:none;border-radius:3px;margin-left:26%;padding:10px 15px}

.works{padding-top:90px;padding-bottom:72px}.works h1{font:bold 18px/24px "Open Sans", sans-serif;text-transform:uppercase;letter-spacing:1px}.works h3{font:bold 25px/30px "Open Sans", sans-serif}.resume__item{text-align:left}.resume__item__title h1{border-bottom:3px solid #11abb0;padding-bottom:6px;display:inline}.resume__item__body>div .item__info{font:16px/24px;color:#6e7881;margin-bottom:18px;margin-top:9px}.resume__item__body>div .item__info span:first-child{font-style:italic;padding-right:10px}.resume__item__body>div .item__info span:first-child:after{content:"•";margin-left:10px}.resume__item__body>div p{font:15px "Open Sans";margin:0 0 30px 0;color:#838c95}

.resume{padding-top:90px;padding-bottom:72px}.resume h1{font:bold 18px/24px "Open Sans", sans-serif;text-transform:uppercase;letter-spacing:1px}.resume h3{font:bold 25px/30px "Open Sans", sans-serif}.resume__item{text-align:left}.resume__item__title h1{border-bottom:3px solid #11abb0;padding-bottom:6px;display:inline}.resume__item__body>div .item__info{font:16px/24px;color:#6e7881;margin-bottom:18px;margin-top:9px}.resume__item__body>div .item__info span:first-child{font-style:italic;padding-right:10px}.resume__item__body>div .item__info span:first-child:after{content:"•";margin-left:10px}.resume__item__body>div p{font:15px "Open Sans";margin:0 0 30px 0;color:#838c95}.skill{margin-top:36px;list-style:none;padding:0}.skill__item{position:relative;margin-bottom:40px;background:#ccc;height:20px;border-radius:3px}.skill__item em{font:15px "opensans-bold", sans-serif;color:#313131;text-transform:uppercase;letter-spacing:2px;font-weight:normal;position:relative;top:-26px}.skill__item span{height:100%;display:block;position:absolute;top:0;border-radius:3px;background-color:#313131}

